/* @jsx jsx */
import {jsx} from "@emotion/react"

import {Link} from "gatsby";

import * as styles from "./App.styles";

export interface AppProps {
    name: string;
    linkToIcon: string;
    linkToHostedApp: string;
}

const App = (props: AppProps) => {
    const {name, linkToIcon, linkToHostedApp} = props;
    return <Link to={linkToHostedApp}>
        <div css={styles.container}>
            <div css={styles.center}>
                <div>
                    <img src={linkToIcon} alt={`${name} icon`} css={styles.imageStyle}/>
                    <div>{name}</div>
                </div>
            </div>
        </div>
    </Link>
}

export default App;
